import { Component, OnInit } from '@angular/core';
import { PaymentService, BankAccount } from '../../payment.service'
import { CommonFunc } from '../../../shared/config'
import { ActivatedRoute, Router } from '@angular/router';
import { modalService } from './../../../services/modalService';


@Component({
  selector: 'app-pay-by-order',
  templateUrl: 'pay-by-order.component.html',
  styleUrls: ['pay-by-order.component.scss']
})
export class PayByOrderComponent implements OnInit {
    public sumPO : number = 0;
    public toggle : string;

    private loadTest : boolean = false
    public companyList : any = [];
    public data : any = [];
    public countSelect : number = 0;
    public summary : number = 0;
    public cardSuccess : boolean = true

  constructor(
      private route : ActivatedRoute,
      private service : PaymentService,
      private func : CommonFunc,
      private bank : BankAccount,
      private modal : modalService
  ) {
      service.listennersSearch().subscribe((val) => {
          this.service.getPTData(this.service.getSelectCompany(), this, true, val);
      })

      route.params.subscribe((params) => {
          let param = params['loadTest']
          if(param) {
              this.loadTest = true;
          }
      })
  }

    private onToggle(select : string) {
      this.toggle = select;
      this.service.getPTData(select, this);
    }

    private onCheckCASH(sapNo : string) {
      this.service.checked(sapNo, this.toggle, this);
    }

    private onCheckCASHALL(sapNo : string) {
        this.service.checked('', this.toggle, this);
    }

    private checkSelect() : void {
      this.countSelect = 0;
        this.bank.statusITSelected = false;
      this.data.map((o, i) => {
          if(o.checked === true) {
              this.countSelect++;
          }
      })

        if(this.countSelect > 0) {
            this.bank.statusITSelected = true;
        }
    }

    private checkSummary() : void {
        let val = 0;
        this.cardSuccess = true
        this.data.map((o, i) => {
            if(o.checked === true) {
                val += o.paidAmt;
            }
        })
        if(val === 0) {
            this.cardSuccess = false
        }
        this.service.sumOrder = val
        this.summary = val;
    }

    public setDataList(data: any = []): void {
      this.data = data;
      this.checkSelect();
      this.checkSummary();
    }

    public setCompanyList(agv : any) : void {
      this.companyList = agv[0];
      this.toggle = agv[1];
      this.service.getPTData(this.toggle, this);
    }

  ngOnInit() {
      if(this.loadTest) {
          this.bank.loadBankTest().then(res => {
          });
      } else {
          $('body').css('background', '#e6e6e6');
          this.bank.loadBank().then(res => {
            if(res.resultCode === 'S') {
              this.bank.setBankAccount(res);
              if (this.service.getInit() === false) {
                  this.service
                      .loadData().then(response => {
                      if (response.resultCode === "S") {
                          this.service
                              .setListData(
                                  response,
                                  this
                              );
                      }
                  });
              } else {
                  this.service.getCompany(this);
                  this.service.getPTData(this.service.getSelectCompany(), this, true);
              }
            } else {
                this.modal.alert([true, res.resultMessage , false, '/index/main']);
            }
          });
      }
  }

}



