import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { CommonFunc } from '../../../shared/config'
import { PaymentService } from '../../payment.service'
import { modalService }  from '../../../services/modalService'

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})

export class CardComponent implements OnInit {
  @Input() PONO: string;
  @Input()
  set Val(Val : number){
    this.valFormat = this.func.formatNumber(Math.abs(Val));
  };
  @Input() Status: string;
  @Input() ListType : string;
  public dateTime_: string;
  @Input() cardCN: boolean;
  @Input() checked: boolean;
  @Input() docTypeAbb : string;
  @Input() disabled : boolean = false;
  public netVal_ : string;
  @Input() docRefNo : string;
  @Input()
  set cashDiscount(cashDiscount : number) {
    this.casDiscount_ = this.func.formatNumber(Number(cashDiscount))
  }
  public casDiscount_ : string = '';
  @Input() max : number = 0;
  @Input()
  set value(value : number){
    if(value > 0) {
      this.value_ = value
    } else {
      this.value_ = undefined
    }
  }
  public value_ : number

  @Output()
  onBill:EventEmitter<any> = new EventEmitter();

  @Input()
  set dateTime(dateTime : string) {
    if(dateTime !== '' && typeof dateTime !== 'undefined') {
      let dateRaw = dateTime.split(' ');
      let dateFormat = dateRaw[0].split('/');
      let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0])
      this.dateTime_ = date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear()
    }
  }

  @Input()
  set netVal(netVal : number) {
    this.netVal_ = this.func.formatNumber(Math.abs(netVal))
  }

  active : boolean = false
  deActive : boolean = true

  @Output()
  onChecked:EventEmitter<string> = new EventEmitter();

  public valFormat : string;
  constructor(
    private func : CommonFunc,
    private paymentService : PaymentService,
    private modalSV : modalService
   ) { }

  public readmore (e): void {
    this.active = this.active !== true;
    this.deActive = this.deActive !== true;
  }

  private onBillChabge(ele : any) {
    const inputValue = parseFloat(ele.target.value.replace(/-/g,''));
    const maxValue = parseFloat(ele.target.max.replace(/-/g,''));
    if(inputValue > maxValue) {
      ele.target.value = undefined;
      this.modalSV.alert([true, 'ไม่สามารถระบุจำนวนเงินมากกว่ามูลค่ารายการที่เลือก']);
      this.onBill.emit([0, this.PONO]);
    } else {
      ele.target.value = parseFloat(inputValue.toFixed(2));
      this.onBill.emit([ele.target.value, this.PONO]);
    }
  }

  ngOnInit() : void {

  }

  private onEleChecked(PONo : string): void {
    this.onChecked.emit(PONo);
  }
}



