import { Component, OnInit } from '@angular/core';
import { PaymentService } from './../../payment.service'

@Component({
  selector: 'app-confrim-cash',
  templateUrl: './confrim-cash.component.html',
  styleUrls: ['./confrim-cash.component.scss']
})
export class ConfrimCashComponent implements OnInit {
  public sumPO : number;
  public summary : number = 0
  public countSelect : number = 0;
  public data : any = [];
  public dataNotCn : any = [];
  public dataCn : any = [];
  public valNotCn : number = 0;
  public valCn : number = 0;

  public setDataList(data: any = []): void {
    this.data = data;
    this.checkSummary();
  }

  private checkSummary() : void {
    this.countSelect = 0;
    this.valNotCn = 0;
    this.valCn = 0
    this.data.map((o, i) => {
      this.countSelect++;
      if(o.checked === true && o.paidAmt >= 0) {
        this.valNotCn += Number(Math.abs(o.paidAmt));
        this.dataNotCn.push(o);
      } else if(o.checked === true && o.paidAmt < 0) {
        this.dataCn.push(o);
        this.valCn += Number(Math.abs(o.billAmt));
      }
    })
    this.summary = Number(this.valNotCn) -  Number(this.valCn);
  }


  constructor(
      private service : PaymentService
  ) { }

  ngOnInit() {
    this.service.getPTDataAll(this.service.getSelectCompany(), this);
  }

}



