import { Component, OnInit } from '@angular/core';
import { PaymentServiceCredit } from './../../payment.service'

@Component({
  selector: 'app-cn-by-credit',
  templateUrl: './cn-by-credit.component.html',
  styleUrls: ['./cn-by-credit.component.scss']
})
export class CnByCreditComponent implements OnInit {
  public sumPO : number;
  public countSelect : number = 0;
  public data : any = [];
  public summary : number = 0
  public summary2 : number = 0
  public stateStatus : boolean = true;
  public cardSuccess : boolean = true

  constructor(
      private service : PaymentServiceCredit
  ) { }

  private checkSelect() : void {
    this.countSelect = 0;
    this.data.map((o, i) => {
      if(o.checked === true) {
        this.countSelect++;
      }
    })

  }

  private onPutBill(agv: any) {
    this.service.billCard(agv[1] , agv[0] , this.service.getSelectCompany(), this, false);
  }

  private onCkecked(invNo: string): void {
    this.service.checked(invNo, this.service.getSelectCompany(), this, false);
  }

  private checkSummary() : void {
    let val = 0;
      this.cardSuccess = true
    this.data.map((o, i) => {
      if(o.checked === true) {
          if(isNaN(Number(o.billAmt)) || Number(Math.abs(o.billAmt)) === 0) {
              this.cardSuccess = false
          }
          val += Number(Math.abs(o.billAmt));
      }
    })
      if(val >= this.service.calValueBill()) {
          this.stateStatus = false
      } else {
          this.stateStatus = true
      }
    this.summary = val;
  }

  public setDataList(data: any = []): void {
    this.data = data;
    this.checkSelect();
    this.checkSummary();
  }



  ngOnInit() {
    this.summary2 = this.service.sumOrder;
    this.service.getPTData(this.service.getSelectCompany(), this, false);
  }

}



