import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PayByOrderComponent } from './cash/pay-by-order/pay-by-order.component'
import { SumByCashComponent } from './cash/sum-by-cash/sum-by-cash.component'
import { CnByCashComponent } from './cash/cn-by-cash/cn-by-cash.component'
import { PaymentComponent } from './payment.component'
import { ConfrimCashComponent } from './cash/confrim-cash/confrim-cash.component'
import { BillComponent } from './cash/bill/bill.component'

import { PayByCreditComponent } from './credit/pay-by-credit/pay-by-credit.component'
import { CnByCreditComponent } from './credit/cn-by-credit/cn-by-credit.component'
import { SumByCreditComponent } from './credit/sum-by-credit/sum-by-credit.component'
import { ConfrimCreditComponent } from './credit/confrim-credit/confrim-credit.component'
import { BillsComponent } from './credit/bill/bills.component'
// import   from '../../assets/images/Banner'
// let contant = new ConstantApp();
let base_img = '../../mypartner/assets/images/'

const paymentRouting: Routes = [
    {
        path: 'cash', component: PaymentComponent,
        children: [
            {
                path: '', component: PayByOrderComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus : true,
                    active: 'cash',
                    tabStatus : true,
                    image : base_img + 'bannerPayment1.png',
                    searchTool : true,
                    alert: {
                        show: false,
                    }
                }
            },{
                path: 'test/:loadTest', component: PayByOrderComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus : true,
                    active: 'cash',
                    tabStatus : true,
                    image : base_img + 'bannerPayment1.png',
                    searchTool : true,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'cn-by-cash', component: CnByCashComponent,
                data: {
                    backLink: '/payment/cash',
                    backLinkStatus : true,
                    active: 'cash',
                    tabStatus : false,
                    image : base_img + 'bannerPayment2.png',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'confrim-cash', component: ConfrimCashComponent,
                data: {
                    backLink: '/payment/cash/cn-by-cash',
                    backLinkStatus : true,
                    active: 'cash',
                    tabStatus : false,
                    image : base_img + 'bannerPayment3.png',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'sum-by-cash', component: SumByCashComponent,
                data: {
                    backLink: '/payment/cash/confrim-cash',
                    backLinkStatus : true,
                    active: 'cash',
                    tabStatus : false,
                    image : '',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'bill-by-cash/:id', component: BillComponent,
                data: {
                    backLink: '/payment/cash/sum-by-cash',
                    backLinkStatus : false,
                    active: 'cash',
                    tabStatus : false,
                    image : '',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            }
        ]
    }, {
        path: 'credit', component: PaymentComponent,
        children: [
            {
                path: '', component: PayByCreditComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : true,
                    image : base_img + 'bannerPayment1.png',
                    searchTool : true,
                    alert: {
                        show: false,
                    }
                }
            }, {
                path: 'test/:loadTest', component: PayByCreditComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : true,
                    image : base_img + 'bannerPayment1.png',
                    searchTool : true,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'cn-by-credit', component: CnByCreditComponent,
                data: {
                    backLink: '/payment/credit',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : false,
                    image : base_img + 'bannerPayment2.png',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'confrim-credit', component: ConfrimCreditComponent,
                data: {
                    backLink: '/payment/credit/cn-by-credit',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : false,
                    image : base_img + 'bannerPayment3.png',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'sum-by-credit', component: SumByCreditComponent,
                data: {
                    backLink: '/payment/credit/confrim-credit',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : false,
                    image : '',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'sum-by-credit/:loadTest', component: SumByCreditComponent,
                data: {
                    backLink: '/payment/credit/confrim-credit',
                    backLinkStatus : true,
                    active: 'credit',
                    tabStatus : false,
                    image : '',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'bill-by-credit/:id', component: BillsComponent,
                data: {
                    backLink: '/payment/credit/sum-by-credit',
                    backLinkStatus : false,
                    active: 'credit',
                    tabStatus : false,
                    image : '',
                    searchTool : false,
                    alert: {
                        show: false,
                    }
                }
            }
        ]
    }, {
        path: '', redirectTo: 'cash'
    }, {
        path: '*', redirectTo: 'cash'
    }
];

@NgModule({
    imports: [RouterModule.forChild(paymentRouting)],
    exports: [RouterModule]
})

export class PaymentRoutingModule {
}



