import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationEnd } from "@angular/router";
import { modalService }  from '../../../services/modalService'
import { PaymentService, PaymentServiceCredit, BankAccount } from '../../payment.service'

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() backLinkStatus: boolean;
  private linkURL_ : string;
  @Input()
  set linkURL(linkURL: string) {
    this.linkURL_ = linkURL
  }
  @Input() active : string;
  @Input() tab : boolean;
  @Input() image : string = '';
  @Input() searchTool : boolean = false;

  private searchBox : boolean = false

  constructor(
      private router : Router,
      private modalSV : modalService,
      private PMService : PaymentService,
      private PMCService : PaymentServiceCredit,
      private bank : BankAccount
  ) { }

  onSearchClick() {
    if(this.searchBox === true) {
      this.PMService.onSerach('')
      this.PMCService.onSerach('')
    }
    this.searchBox = this.searchBox === false
  }

  onSearch(e) {
    this.PMService.onSerach(e.target.value)
    this.PMCService.onSerach(e.target.value)
  }

  ngOnInit() {
  }

  onChangeMenu(link : string) : void {
    if(this.router.url !== link && this.bank.statusITSelected) {
      this.modalSV.alert([true, 'ระบบจะทำการยกเลิกข้อมูลที่เลือกไว้้ต้องการทำรายการต่อไปหรือไม่', true, link]);
    } else {
      this.router.navigate([link])
    }
  }

  toBackLink(){
      this.router.navigate([this.linkURL_]);
  }

}



