import { Component, OnInit } from '@angular/core';
import { PaymentServiceCredit } from './../../payment.service'

@Component({
  selector: 'app-confrim-credit',
  templateUrl: './confrim-credit.component.html',
  styleUrls: ['./confrim-credit.component.scss']
})
export class ConfrimCreditComponent implements OnInit {
  public sumPO : number;
  public summary : number = 0
  public countSelect : number = 0;
  public data : any = [];
  public dataNotCn : any = [];
  public dataCn : any = [];
  public valNotCn : number = 0;
  public valCn : number = 0;
  public valDiscount : number = 0;




  public setDataList(data: any = []): void {
    this.data = data;
    this.checkSummary();
  }

  private checkSummary() : void {
    this.countSelect = 0;
    this.valNotCn = 0;
    this.valCn = 0
    this.valDiscount = 0;
    this.data.map((o, i) => {
      this.countSelect++;
      if(o.checked === true && o.docAmt >= 0) {
        this.valNotCn += Number(Math.abs(o.billAmt));
        this.valDiscount += Number(Math.abs(o.discount));
        this.dataNotCn.push(o);
      } else if(o.checked === true && o.docAmt < 0) {
        this.dataCn.push(o);
        this.valCn += Number(Math.abs(o.billAmt));
      }
    })
    this.summary = this.valNotCn - (this.valCn);
  }

  constructor(
      private service : PaymentServiceCredit
  ) { }

  ngOnInit() {
    this.service.getPTDataAll(this.service.getSelectCompany(), this);
  }

}



