@Injectable()
export class BankAccount {
    public statusITSelected = false;
    private bankAccount : any = [];
    private selectBankList : string = '';
    private selectedBank : string = '';
    private selectCode : string = '';

    constructor(
        private http : Http,
        private constant : ConstantApp,
        private cookie : CookieService,
        private func : CommonFunc,
        private service : UserInfo,
        private authHttpService : AuthHttpService
    ) {

    }

    public clearState() {
        this.selectedBank = '';
        this.selectBankList = '';
    }

    public setSelectBankList(num : string):void {
        this.selectBankList = num;
    }

    public setSelectedBank(bank : string):void {
        this.selectedBank = bank;
    }

    public getSelectBankList() {
        return this.selectBankList
    }

    public getSelectBank() {
        return this.selectedBank;
    }

    public getSelectCode() {
        return this.selectCode;
    }

    public setSelectCode(code : string): void {
        this.selectCode = code
    }

    public getBankAccount(companySelect : string) {
        let response = [];
        this.bankAccount.filter((e) => {
            if(e.company === companySelect) {
                response = e.list
            }
        })
        return response;
    }



    public setBankAccount(agv : any) : void {
        this.bankAccount = [];
        agv.listBankAccount.map((obj, i) => {
            let Array = [];
            obj.listDealerBank.map((rec, index) => {
                if(typeof Array[rec.bankCode] === 'undefined') {
                    Array[rec.bankCode] = [];
                }
                Array[rec.bankCode].push({
                    accountNum : rec.accountNum,
                    accountCode : rec.accountCode,
                    checked : false
                });
            });
            this.bankAccount.push({
                company : obj.company,
                list : Array
            });
        });
    }

    public checkPermissionBank(company: string)  {
        let list =  this.getBankAccount(company);
        return (Object.keys(list).length > 0 ? true : false);
    }

    public loadBankTest() {
        let data = {
        };

        return this.authHttpService.post(this.constant.URL_BANKTEST,
            data).toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    public loadBank() {
        let data = {
            "userName" : this.service.getUsername(),
            'temp' : this.service.gToken(this.service.getUsername()),
            'temp_' : this.service.getAccessToken()
        }; 

        this.func.loadingStart();
        return this.authHttpService.post(this.constant.URL_BANK,
            data).toPromise()
            .then((res) => {
                this.func.loadingStop();
               return res;
        })
            .catch((error) => {
                this.func.loadingStop();
                this.handleError(error)
                }
            );
    }

    private extractData(res){
        return res;
    }

    private getPutHeaders(){
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return  new RequestOptions({
            headers: headers
        });
    }

    private handleError(error: Response | any){
        let errMsg: string;
        if (error instanceof Response){
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
    //
}


@Injectable()
export class PaymentService {

    private blankAccount : any = {};
    private dataListCash : any = [];
    private init : boolean = false;
    private permission : boolean = false;
    private DTCForUse : any = [];
    private selectCompany : string = '';
    private defaultData : any = {};
    private subject : any = new Subject<any>();
    public sumOrder : number = 0;
    private dateServer : string = '';

    constructor(
        private router: Router,
        private http : Http,
        private constant : ConstantApp,
        private cookie : CookieService,
        private func : CommonFunc,
        private bank : BankAccount,
        private service : UserInfo,
        private modal : modalService,
        private authHttpService : AuthHttpService
    ) {
    }

    public listennersSearch(): Observable<any> {
        return this.subject.asObservable();
    }

    public onSerach(val: string): void {
        this.subject.next(val);
    }

    private clearStore() : void {
        this.DTCForUse = [];
        this.blankAccount = [];
        this.dataListCash = [];
    }

    public summaryData() {
        let scope = this
        let listCompany = this.defaultData.paymentItemCompanySummaryList.filter((e) => {
            if(e.company === this.getSelectCompany()) {
                return true
            }
        })
        let companyName = this.getSelectCompany();
        let structure : any = {}
        let that = this;
        let totalOrder : number = 0;
        let totalCn : number = 0;
        let totalDn : number = 0;
        let TotalDiscount : number = 0;
        let TotalInterest : number = 0;
        let TotalPayAmt : number = 0;

        let sapReceiptDtlList : any = [];

        this.DTCForUse.filter(function(e) {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                    if(o.checked === true) {
                        if(o.docTypeAbb === 'C/N' && !that.conditionShowCn(o, e.value)) {
                            return;
                        }
                        switch (o.docTypeAbb) {
                            case "ORD":
                                totalOrder += Number(o.paidAmt);
                                break;
                            case "C/N" :
                                totalCn += -Math.abs(o.billAmt)
                                break;
                            case "REB" :
                                totalCn += -Math.abs(o.billAmt)
                                break;
                            case "D/N" :
                                totalDn += Number(o.paidAmt)
                                break;
                            default :
                                break;
                        }
                        sapReceiptDtlList.push({
                            docNo : o.sapOrderNo || '',
                            docType : o.docTypeAbb || '',
                            docRefNo : o.docRefNo || '',
                            docDate : o.orderDate || '',
                            // docAmt : o.paidAmt || '',
                            docAmt : o.netPrice || '',
                            docDiscountAmt : Number(o.cashDiscount).toFixed(2),
                            docDiscountPercent : Number(o.percentCashDiscount).toFixed(2),
                            paidAmt : (o.docTypeAbb !== 'C/N' && o.docTypeAbb !== 'REB' ? o.paidAmt : 0) || 0,
                            docLineNo : o.lineNo || '',
                            docPoNo : o.poNo || '',
                            productType : o.productTypeAbb || '',
                            payInvAmtCN : -Number(o.docTypeAbb === 'C/N' || o.docTypeAbb === 'REB' ? o.billAmt : 0)
                        });
                    }
                });
            }
        });


        TotalPayAmt = Number(totalOrder) + Number(totalCn) + Number(totalDn)

        structure = {
            "company": this.getSelectCompany(),
            "dealerCode": this.service.getDealerCode(),
            "locationCode": this.service.getLocationCode(),
            "totalOrder": Number(Number(totalOrder).toFixed(2)),
            "totalCn": Number(Number(totalCn).toFixed(2)),
            "totalDn": Number(Number(totalDn).toFixed(2)),
            "totalDiscount": TotalDiscount.toString(),
            "totalInterest": TotalInterest.toString(),
            "totalPayAmt": Number(Number(TotalPayAmt).toFixed(2)),
            "paymentCat": "CASH",
            "accountCode": this.bank.getSelectCode(),
            "userName": this.service.getUsername(),
            "overdueWDS" : listCompany[0].overdueWDS.toString(),
            "overdueAWN_T" : listCompany[0].overdueAWN_T.toString(),
            "overdueAWN_O" : listCompany[0].overdueAWN_O.toString(),
            "productType": "OTH",
            "sapReceiptDtlList": sapReceiptDtlList,
            'temp' : this.service.gToken(this.service.getUsername(), this.service.getLocationCode().toString()),
            'temp_' : this.service.getAccessToken()
        }

        this.func.loadingStart();

        return this.authHttpService.post(this.constant.URL_UPDATEPAYMENTCASH,
            structure).toPromise()
            .then((res) => {
                this.func.loadingStop();
                return res;
            })
            .catch((error) => {
                this.func.loadingStop(); 
                this.modal.alert([true, 'เกินเวลาที่กำหนด กรุณาลองใหม่']);  
                this.handleError(error)
            });
    }

    public getDateServer() {
        return this.dateServer;
    }c

    public billCard(poNO: string = '', value : number , companyName : string, that : any,  positive: boolean = true) {
        this.DTCForUse.filter(function(e) {
            if(e.name === companyName) {
                e.value.map(function(o, i) {
                    if(o.sapOrderNo === poNO) {
                        o.billAmt = value;
                    }
                });
            }
        });
        this.getPTData(companyName, that, positive);
    }

    public setListData(obj : any , that : any) {
        let scope = this;
        this.clearStore();
        let permission : number = 0;

        let dateRaw = obj.serverDateTime.split(' ');
        let dateFormat = dateRaw[0].split('/');
        let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0]);
        this.dateServer = date.getFullYear() + '/' +
            this.func.getFullMounth(date.getMonth()) +
            '/' + date.getDate();

        obj.paymentItemCompanySummaryList.map(function(o, i) {
            if(scope.bank.checkPermissionBank(o.company)) {
                let countPositive = 0;
                let countNegative = 0;

                scope.DTCForUse.push({
                    name: o.company,
                    value: o.paymentItemSummaryDetailList.map(function (oo, ii) {
                        let dateRaw = [];
                        let dateFormat = [];
                        let ddFormat = '';
                        let overdue : boolean = false;
                        if(typeof oo.dueDate === 'string' && oo.dueDate !== '') {
                            dateRaw = oo.dueDate.split(' ');
                            dateFormat = dateRaw[0].split('/');
                            let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0]);
                            ddFormat = date.getFullYear() + '/' + scope.func.getFullMounth(date.getMonth()) + '/' + date.getDate()
                        }

                        let dateRaw_ = [];
                        let dateFormat_ = [];
                        let dtFormat = '';

                        if(typeof oo.orderDate === 'string' && oo.orderDate !== '') {
                            dateRaw_ = oo.orderDate.split(' ');
                            dateFormat_ = dateRaw_[0].split('/');
                            let date = new Date(dateFormat_[2] + '/' + dateFormat_[1] + '/' + dateFormat_[0]);
                            dtFormat = date.getFullYear() + '/' + scope.func.getFullMounth(date.getMonth()) + '/' + date.getDate()
                        }

                        let d = new Date(scope.getDateServer());
                        let dStr = d.getFullYear() + '/' + scope.func.getFullMounth(d.getMonth()) + '/' + d.getDate()
                        if(dStr >= ddFormat) {
                            overdue = true
                        }
                        if (oo.paidAmt >= 0 && dtFormat < dStr) {
                            countPositive++;
                        } else if(oo.docAmt < 0) {
                            countNegative++;
                        }
                        oo.checked = false;
                        oo.billAmt = 0;
                        oo.max = Math.abs(oo.paidAmt)
                        return oo;
                    }),
                    countPositive: countPositive,
                    countNegative: countNegative
                });
                permission++;
            }
        });
        if(permission === 0) {
            this.modal.alert([true, 'ไม่พบรายการธนาคาร ระบบจึงไม่อนุญาติให้เข้าถึงการจ่ายเงิน', false, '/index/main']);
        } else {
            this.init = true;
            this.defaultData = obj;
        }
        that.setCompanyList(this.getCompanies());
    }

    private findRefDoc(sapNo: string, companyName: string, docType: string, checked : boolean) : void {
        let pono : string = '';
        let listSapNo : any = [];

        if(("C/N" === docType) || ("D/N" === docType && checked === false) ) {
            return;
        }

        this.DTCForUse.filter((e) => {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                    if(
                        o.docTypeAbb === 'ORD' &&
                        o.sapOrderNo === sapNo &&
                        pono === '' &&
                        o.disabled === false
                    ) {
                        pono = o.poNo;
                        o.checked = checked
                    }
                });
            }
        });

        this.DTCForUse.filter((e) => {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                    if(o.disabled === true) {
                        return false;
                    }

                    if(
                        o.docTypeAbb === 'ORD' &&
                        typeof pono !== 'undefined' &&
                        pono !== '' &&
                        pono === o.poNo &&
                        o.disabled === false
                    ) {
                        o.checked = checked
                        if(typeof o.sapOrderNo !== 'undefined') {
                            listSapNo.push(o.sapOrderNo);
                        }
                    }
                });
            }
        });


        this.DTCForUse.filter((e) => {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                    if(o.disabled) {
                        return false 
                    }

                    if(
                        docType === 'D/N' ||
                        ("ORD" === docType && checked === false ) ||
                        docType === 'C/N'
                    ) {

                        if("D/N" === o.docTypeAbb) {
                            if(
                                (typeof sapNo === 'string' && sapNo !== '') &&
                                sapNo === o.docRefNo
                            ) {
                                o.checked = checked
                                if(checked === false) {
                                    o.billAmt = 0
                                }
                            }

                            listSapNo.map((row, rec) => {
                                if(row === e.docRefNo) {
                                    o.checked = checked
                                    if(checked === false) {
                                        o.billAmt = 0
                                    }
                                }
                            });
                        }
                    }


                    if(
                        "C/N" === docType ||
                        ("ORD" === docType && checked === false) ||
                        ("D/N" === docType && checked === false)
                    ) {
                       if("ORD" !== o.docTypeAbb || "D/N" === o.docTypeAbb) {
                           listSapNo.map((row, rec) => {
                               if(row === e.docRefNo) {
                                   o.checked = checked
                               }
                           });
                       }
                    }

                });
            }
        });
    }

    public checked(sapNo: string = '', companyName : string, that : any, positive: boolean = true) {
        let varThis = this
        this.DTCForUse.filter(function(e) {
            if(e.name === companyName) {
                e.value.map(function(o, i) {
                    if(sapNo === '') {
                        o.checked = true;
                        return true;
                    }
                    if(o.sapOrderNo === sapNo) {
                        o.checked = !o.checked;

                        varThis.findRefDoc(o.sapOrderNo, companyName, o.docTypeAbb, o.checked);
                        if(o.checked === false) {
                            o.billAmt = 0
                        }
                    }
                });
            }
        });
        this.getPTData(companyName, that, positive);
    }

    public calValueBill() {
        let companyName = this.getSelectCompany();
        let response : any = {};
        response.totalCash = 0;

        this.DTCForUse.filter((e) => {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                    if(o.paidAmt >= 0 && o.checked) {
                        response.totalCash += o.paidAmt ;
                    }
                })
            }
        })
        return response.totalCash;
    }

    public calSummaryData(that: any) {
        let companyName = this.getSelectCompany();
        let response : any = {};
        let varThis = this;
        response.discount = 0;
        response.totalCash = 0;


        this.DTCForUse.filter((e) => {
            if(e.name === companyName) {
                e.value.map((o, i) => {
                  if(o.paidAmt >= 0 && o.checked) {
                      response.totalCash += o.paidAmt ;
                  }  else if(o.checked) {
                      if(varThis.conditionShowCn(o, e.value) === true) {
                          response.discount += Math.abs(o.billAmt);
                      }
                  }
                })
            }
        })
        that.setSummaryDetail(response);
    }

    private conditionShowCn(agv : any, data : any) {
        let status = false;
        let statusCase3 = true;
        if(agv.docTypeAbb !== 'REB' && agv.docTypeAbb !== 'C/N') {
            return false
        }
        if((typeof agv.docRefNo === 'undefined' || agv.docRefNo === '') && agv.docTypeAbb === 'C/N') {
            status = true;
        } else if(agv.docTypeAbb === 'REB') {
            status = true;
        } else {
            for (let i in data) {
                if (
                    ((data[i].docTypeAbb === 'ORD' || data[i].docTypeAbb === 'D/N') &&
                    data[i].checked === true) &&
                    ( data[i].sapOrderNo === agv.docRefNo ) && agv.docTypeAbb === 'C/N'
                ) {
                    status = true;
                }

                if(agv.docTypeAbb === 'C/N' && agv.sapOrderNo === data[i].docRefNo) {
                    statusCase3 = false;
                }
            }
        }
        return statusCase3 ? statusCase3 : status;
    }

    public getPTData(companyName: string, that : any, positive: boolean = true, search : string = '') {
        this.selectCompany = companyName;
        let varThis = this;
        this.DTCForUse.filter(function(e) {
            if(e.name === companyName) {
                 that.setDataList(e.value.filter(function(o, i) {
                 if(typeof o === 'undefined') { return false }
                    if(o.paidAmt >= 0 && positive === true) {
                        if(search === '') {
                            return o
                        } else if(varThis.serachField(o.sapOrderNo, search)) {
                            return o
                        }
                    } else if (o.paidAmt < 0 && positive === false) {
                        if(varThis.conditionShowCn(o, e.value) === true) {
                            return o
                        } else {
                            o.billAmt = 0
                            o.checked = false
                        }
                    }
                }));
            }
        });
    }

    public getCompany(that : any) {
        that.setCompanyList(this.getCompanies());
    }

    private getCompanies() {
        let arr = [];
        let df = '';
        if(this.selectCompany !== '') {
            df = this.selectCompany;
        }
        this.DTCForUse.map(function(o, i) {
            if(df === '') {
                df = o.name;
            }
            arr.push({
                name : o.name,
                count : o.countPositive
            })
        });
        this.selectCompany = df;
        return [arr, df];
    }

    public getInit() {
        if(!this.init) {
            this.selectCompany = '';
            this.clearStore();
            this.bank.clearState();
        }
        return this.init;
    }

    public setInit(bool : boolean = false):void {
        this.init = bool
    }

    public getSelectCompany() {
        return this.selectCompany;
    }

    public serachField(val: string, sTxt : string) {
        let strArr = val.search(sTxt)
        return strArr > -1
    }

    public loadData() {
        let data = {
          "dealerCode" : this.service.getDealerCode(),
          "type" : "CASH",
          "userName" : this.service.getUsername(),
          'temp' : this.service.gToken(this.service.getUsername()),
          'temp_' : this.service.getAccessToken()
        };

        this.func.loadingStart();
        return this.authHttpService.post(this.constant.URL_PAYMENT,
            data).toPromise()
            .then((res) => {
                this.func.loadingStop();
                return res;
            })
            .catch((error) => {this.func.loadingStop(); this.handleError(error)});
    }

    private extractData(res){
        return res;
    }

    private getPutHeaders(){
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return  new RequestOptions({
            headers: headers
        });
    }

    private handleError(error: Response | any){
        let errMsg: string;
        if (error instanceof Response){
          const body = error.json() || '';
          const err = body.error || JSON.stringify(body);
          errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
          errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }


    public getPTDataAll(companyName: string, that : any) {
        this.selectCompany = companyName;
        let varThis = this;
        this.DTCForUse.filter(function(e) {
            if(e.name === companyName) {
                that.setDataList(e.value.filter(function(o, i) {
                    if(typeof o === 'undefined') { return false }
                    if(o.paidAmt >= 0 && o.checked) {
                        o.cardCN = false;
                        return o
                    } else if (o.paidAmt < 0 && o.checked) {
                        if(varThis.conditionShowCn(o, e.value) === true) {
                            o.cardCN = true;
                            return o
                        }
                    }
                }));
            }
        });
    }

}







