import { Component, OnInit, Input } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonFunc } from './../../../shared/config'
import { PaymentServiceCredit, PaymentService } from '../../payment.service'
import { modalService } from '../../../services/modalService'

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  public sumPO_: string = '';
  public sumPO2_: string = '';
  public discount_: string = '';
  public cn_: string = '';
  public total_: string = '';

  public debtSum_: string = ''
  public debtReduce_: string = ''

  @Input() stateStatus: boolean = true;
  @Input() cardSuccess: boolean = true;


  @Input() name: string;
  @Input() link: string;
  @Input() cardCN: boolean;
  @Input() detailCard: boolean = false;

  @Input() footerTxt: string;
  @Input() footerTxt2: string = '';
  @Input() backLink: boolean = false;
  @Input() backUrl: string;

  @Input()
  set debtSum(debtSum: number) {
    this.debtSum_ = this.func.formatNumber(Number(debtSum));
  }

  @Input()
  set debtReduce(debtReduce: number) {
    this.debtReduce_ = this.func.formatNumber(Number(debtReduce));
  }


  @Input()
  set discount(discount: number) {
    this.discount_ = this.func.formatNumber(Number(discount))
  }
  @Input()
  set cn(cn: number) {
    this.cn_ = this.func.formatNumber(Number(cn))
  }
  @Input()
  set total(total: number) {
    this.total_ = this.func.formatNumber(Number(total))
  }
  @Input()
  set sumPO(sumPO: string) {
    this.sumPO_ = this.func.formatNumber(Number(sumPO))
  }

  @Input()
  set sumPO2(sumPO2: string) {
    this.sumPO2_ = this.func.formatNumber(Number(sumPO2))
  }

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private func: CommonFunc,
    private serviceCash: PaymentService,
    private serviceCredit: PaymentServiceCredit,
    private modalSV: modalService,
    private _location: Location
  ) {
  }

  private cancelState() {
    this.serviceCash.setInit();
    this.serviceCredit.setInit();
    this.router.navigate([this.backUrl]);
    // this._location.back();
  }

  back() {
    
  }

  onLink() {
    if (this.stateStatus && this.cardSuccess) {
      this.router.navigate([this.link]);
    } else if (!this.stateStatus) {
      this.modalSV.alert([true, 'ไม่สามารถใช้ส่วนลดมากกว่ารายจ่าย']);
    } else if (!this.cardSuccess) {
      this.modalSV.alert([true, 'มีรายการที่ยังไม่สมบูรณ์กรุณากรอกค่าให้ครบ']);
    }
  }

  ngOnInit() {
  }

}



