import { Component, OnInit } from '@angular/core';
import { PaymentService } from './../../payment.service'
import { CommonFunc } from './../../../shared/config'
import { ActivatedRoute, Router } from '@angular/router';
import { modalService } from '../../../services/modalService'
import { DataForm } from '../../../services/form'
import { UserInfo } from '../../../services/userInfo'

@Component({
  selector: 'app-sum-by-cash',
  templateUrl: 'sum-by-cash.component.html',
  styleUrls: ['sum-by-cash.component.scss']
})
export class SumByCashComponent implements OnInit {
  public totalCash: string = '0'
  public discount: string = '0';
  public summary: string = '0';
  public selectCompany: string;
  private stateValidator: boolean = false;

  constructor(
    private service: PaymentService,
    private func: CommonFunc,
    private router: Router,
    private modalSV: modalService,
    private form: DataForm,
    private user: UserInfo
  ) { }

  public setSummaryDetail(agv: any) {
    this.totalCash = this.func.formatNumber(agv.totalCash)
    this.discount = this.func.formatNumber(agv.discount);
    this.summary = this.func.formatNumber(agv.totalCash - agv.discount);
  }

  onLink() {
    if (this.stateValidator) {
      this.service.summaryData().then(async (res) => {
        const response = await res;


        if (typeof response !== 'undefined' && response.resultCode === 'S') {
          let routerLink = await '/payment/cash/bill-by-cash/' + response.sapTmNumberList.map((o, i) => {
            return o.sapTmNumber;
          }).toString();
          $("#preload").css("display", "block");
          this.form.getProfileInfo(this.user.getLocationCode()).then(async cb => {
            $("#preload").css("display", "none");
            if (typeof cb.resultCode === 'string' && cb.resultCode === 'S') {
              await this.user.reCompanyProfile(cb);
              await this.router.navigate([routerLink]);
            } else {
              await this.router.navigate(['/index']);
            }
          })
        } else {
          await this.modalSV.alert([true, JSON.stringify(res.resultMessage)]);
          await setTimeout(() => {
            this.router.navigate(['/index']);
          }, 4000)
        }
      })
    } else {
      this.modalSV.alert([true, 'กรุณาเลือกบัญชี']);
    }
  }

  ngOnInit() {
    this.selectCompany = this.service.getSelectCompany();
    this.service.calSummaryData(this);
  }

  onhandleState(event: boolean) {
    this.stateValidator = event
  }

  delay() {
    let status = false;
    setTimeout(() => {
      status = true;
    }, 4000)
    return status;
  }
}



