import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule } from '@angular/router';

import { ConstantApp } from '../constant/ConstantApp'
import { PaymentRoutingModule } from './payment-module-routing.module'
import { PaymentComponent } from './payment.component'
import { PayByOrderComponent } from './cash/pay-by-order/pay-by-order.component';
import { HeaderComponent } from './shared/header/header.component';
import { CardComponent } from './shared/card/card.component';
import { FooterComponent } from './shared/footer/footer.component';
import { PaymentService, PaymentServiceCredit, BankAccount } from './payment.service';
import { CommonFunc } from '../shared/config';
import { CnByCashComponent } from './cash/cn-by-cash/cn-by-cash.component';
import { SumByCashComponent } from './cash/sum-by-cash/sum-by-cash.component';
import { PayByCreditComponent } from './credit/pay-by-credit/pay-by-credit.component';
import { CnByCreditComponent } from './credit/cn-by-credit/cn-by-credit.component';
import { SumByCreditComponent } from './credit/sum-by-credit/sum-by-credit.component';
import { PartialCardComponent } from './shared/partial-card/partial-card.component';
import { ConfrimCashComponent } from './cash/confrim-cash/confrim-cash.component';
import { ConfrimCreditComponent } from './credit/confrim-credit/confrim-credit.component';
import { DetailCardComponent } from './shared/detail-card/detail-card.component';
import { TopbarSharedModule } from '../shared/topbar.module';
import { modalService } from '../services/modalService'
import { BankAccountComponent } from './shared/bank-account/bank-account.component'
import { CustomPipe } from '../customPipe';
import { BillComponent } from './cash/bill/bill.component'
import { BillsComponent } from './credit/bill/bills.component'
import { ServiceShared } from '../shared/service.module'
import { DataForm } from '../services/form'


@NgModule({
  imports: [
    CommonModule,
    TopbarSharedModule,
    PaymentRoutingModule,
    RouterModule,
    ServiceShared
  ],
  declarations: [
    PayByOrderComponent, 
    HeaderComponent, 
    PaymentComponent, 
    CardComponent, 
    FooterComponent, 
    CnByCashComponent, 
    SumByCashComponent, 
    PayByCreditComponent, 
    CnByCreditComponent, 
    SumByCreditComponent, 
    PartialCardComponent, 
    ConfrimCashComponent, 
    ConfrimCreditComponent, 
    DetailCardComponent, 
    BankAccountComponent, 
    CustomPipe, 
    BillComponent, 
    BillsComponent
  ],
  providers: [ 
    PaymentService, 
    CommonFunc, 
    ConstantApp, 
    PaymentServiceCredit, 
    BankAccount, 
    modalService, 
    DataForm 
  ]
})
export class PaymentModule { }



