import { Component, OnInit, Input, EventEmitter, Output, ChangeDetectorRef } from '@angular/core';
import { CommonFunc } from '../../../shared/config'
import { PaymentService } from '../../payment.service'
import { modalService }  from '../../../services/modalService'
declare let $: any;


@Component({
  selector: 'app-partial-card',
  templateUrl: './partial-card.component.html',
  styleUrls: ['./partial-card.component.scss']
})
export class PartialCardComponent implements OnInit {
  @Input() PONO: string;
  @Input() Val: number = 0;
  @Input()
  set netVal(netVal: number) {
    this.netVal_ = this.func.formatNumber(Math.abs(netVal));
  }
  @Input() disabled : boolean = false;
  public netVal_ : string = ''
  @Input() Status: string;
  @Input() ListType : string;
  private overdue : boolean = false;
  @Input() cardCN: boolean;
  @Input()
  set docDueDistDate (docDueDistDate : string) {
      if(typeof docDueDistDate === 'string' && docDueDistDate !== '') {
          let dateRaw = docDueDistDate.split(' ');
          let dateFormat = dateRaw[0].split('/');
          let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0]);
          this.docDueDistDate_ = date.getDate() + '/' + this.func.getFullMounth(date.getMonth()) + '/' + date.getFullYear()
      }
  }
  public docDueDistDate_ : string = '';
  @Input() docTypeAbb: string;
  public billAmt :number;
    @Input()
    set value(value : number){
        if(value > 0) {
            this.value_ = value
        } else {
            this.value_ = undefined
        }
    }
    public value_ : number
  @Input() max : number = 0;
  @Input() min : number = 0;
  @Input() checked: boolean;
  @Input() discountStr: string = '';

  @Input() docRefNo: string = '';
  @Input()
  set dueDate (dueDate: string){
    let dateRaw = dueDate.split(' ');
    let dateFormat = dateRaw[0].split('/');
    let ddFormat = '';
    if(typeof dueDate === 'string' && dueDate !== '') {
        let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0]);
        this.dueDate_ = date.getDate() + '/' + this.func.getFullMounth(date.getMonth()) + '/' + date.getFullYear()
        ddFormat = date.getFullYear() + '/' + this.func.getFullMounth(date.getMonth()) + '/' + date.getDate()
    }
      let d = new Date();
      let dStr = d.getFullYear() + '/' + this.func.getFullMounth(d.getMonth()) + '/' + d.getDate()
      if(ddFormat <= dStr) {
          this.overdue = true
      }

  }
  public dueDate_ : string = '';
  public docDate_ : string = '-'
  @Input()
  set docDate(docDate: string) {
    let dateRaw = docDate.split(' ');
    let dateFormat = dateRaw[0].split('/');
    if(typeof docDate === 'string' && docDate !== '') {
      let date = new Date(dateFormat[2] + '/' + dateFormat[1] + '/' + dateFormat[0]);
      this.docDate_ = date.getDate() + '/' + this.func.getFullMounth(date.getMonth()) + '/' + date.getFullYear()
    }
  }




  active : boolean = false
  deActive : boolean = true

  @Output()
  onChecked:EventEmitter<string> = new EventEmitter();

  @Output()
  onBill:EventEmitter<any> = new EventEmitter();

  public valFormat : string;
  constructor(
      private func : CommonFunc,
      private paymentService : PaymentService,
      private cdRef:ChangeDetectorRef,
      private modalSV : modalService
  ) { }

  ngOnInit() : void {
    this.valFormat = this.func.formatNumber(Math.abs(this.Val));
  }

  public readmore (e): void {
    this.active = this.active !== true;
    this.deActive = this.deActive !== true;
  }

  private onBillChabge(ele : any) {
    console.log(ele);
    console.log(ele.target.value);
    console.log(ele.target.max);
    const inputValue = parseFloat(ele.target.value.replace(/-/g,''));
    const maxValue = parseFloat(ele.target.max.replace(/-/g,''));
    console.log(inputValue);
    if(inputValue > maxValue) {
      ele.target.value = undefined;
      this.modalSV.alert([true, 'ไม่สามารถระบุจำนวนเงินมากกว่ามูลค่ารายการที่เลือก']);
      this.onBill.emit([0, this.PONO]);
    } else {
      ele.target.value = parseFloat(inputValue.toFixed(2));
      console.log(ele.target.value);
      this.onBill.emit([ele.target.value, this.PONO]);
    }
  }

  private onEleChecked(PONo : string): void {
    this.onChecked.emit(PONo);
  }
}





