import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { PaymentServiceCredit, BankAccount } from '../../payment.service'
import { CommonFunc } from '../../../shared/config'
import { ActivatedRoute, Router } from '@angular/router';
import { modalService } from './../../../services/modalService'

@Component({
  selector: 'app-pay-by-credit',
  templateUrl: './pay-by-credit.component.html',
  styleUrls: ['./pay-by-credit.component.scss']
})
export class PayByCreditComponent implements OnInit {
  public sumPO : number = 0;
  public toggle : string;
  private loadTest : boolean = false

  public companyList : any = [];
  public data : any = [];
  public countSelect : number = 0;
  public summary : number = 0;
  public test : number = 1;
  public max : number = 10;
  public cardSuccess : boolean = true
  public dataNotFound : boolean = true

  constructor(
      private route : ActivatedRoute,
      private service : PaymentServiceCredit,
      private func : CommonFunc,
      private cdRef:ChangeDetectorRef,
      private bankService : BankAccount,
      private modal : modalService
  ) {
    service.listennersSearch().subscribe((val) => {
      this.service.getPTData(this.service.getSelectCompany(), this, true, val);
    })

    route.params.subscribe((params) => {
      let param = params['loadTest']
      if(param) {
        this.loadTest = true;
      }
    })
  }

  private onPutBill(agv: any) {
    this.service.billCard(agv[1] , agv[0] , this.toggle, this);
  }


  private onToggle(select : string) {
    this.toggle = select;
    this.service.getPTData(select, this);
  }

  private onCheckCASH(invNo : string) {
    this.service.checked(invNo, this.toggle, this);
  }


  private checkSelect() : void {
    this.countSelect = 0;
    this.bankService.statusITSelected = false;
    this.data.map((o, i) => {
      if(o.checked === true) {
        this.countSelect++;
      }
    })
    if(this.countSelect > 0) {
      this.bankService.statusITSelected = true;
    }
  }

  private checkSummary() : void {
    let val = 0;
    this.cardSuccess = true
    this.data.map((o, i) => {
      if(o.checked === true) {
        if(isNaN(Number(o.billAmt)) || Number(Math.abs(o.billAmt)) === 0) {
          this.cardSuccess = false
        }
        val += Number(o.billAmt);
      }
    })

    if(val === 0) {
      this.cardSuccess = false
    }
    this.service.sumOrder = val
    this.summary = val;
  }

  public setDataList(data: any = []): void {
    this.data = data;

    this.checkSelect();
    this.checkSummary();
  }

  public setCompanyList(agv : any) : void {
    this.companyList = agv[0];
    this.toggle = agv[1];
    this.service.getPTData(this.toggle, this);
  }

  ngOnInit() {

      if (this.loadTest) {
        this.service
            .loadDataTest().then(response => {
        });
      } else {
        $('body').css('background', '#e6e6e6');

        this.bankService.loadBank().then(res => {
          if(res.resultCode === 'S') {
          this.bankService.setBankAccount(res);

          if (this.service.getInit() === false) {
            this.service
                .loadData().then(response => {
              if (typeof response.resultCode !== 'undefined' && response.resultCode === "S") {
                this.service
                    .setListData(
                        response,
                        this
                    );
              }
            });

          } else {
            this.service.getCompany(this);
            this.service.getPTData(this.service.getSelectCompany(), this, true);
          }
        } else {
          this.modal.alert([true, res.resultMessage , false, '/index/main']);
        }
        });
      }
    }

}



