import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonFunc } from './../../../shared/config'
import { PaymentServiceCredit } from './../../payment.service'
import { BankAccount } from '../../payment.service'

@Component({
  selector: 'app-bills',
  templateUrl: './bills.component.html',
  styleUrls: ['./bills.component.scss']
})
export class BillsComponent implements OnInit {
  private dateTime : string;
  public summary : string = '0';
  private bank : string = '';
  private bankCode : string = '';
  private OrdNo : string = '';

  constructor(
      private router:Router,
      private service : PaymentServiceCredit,
      private func : CommonFunc,
      private bankSV : BankAccount,
      private route: ActivatedRoute
  ) {
    route.params
        .subscribe(params => {
          this.OrdNo = params['id'];
        });
    let d = new Date();
    this.dateTime = this.formatAMPM(d);

  }

  public setSummaryDetail(agv: any) {
    this.summary = this.func.formatNumber(agv.totalCash - (agv.cn));
  }

  private formatAMPM(date) {
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? 'pm' : 'am';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? '0'+minutes : minutes;
    var strTime = date.getDate() + "/" + (date.getMonth() + 1 ) + "/" + date.getFullYear() + " " + hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }

  private onSuccess() {
    this.router.navigate(['/payment/credit'])

  }

  ngOnInit() {
    this.service.setInit(false);
    $('body').css('background', 'white');
    this.service.calSummaryData(this);
    this.bank = this.bankSV.getSelectBank();
    this.bankCode = this.bankSV.getSelectBankList();
  }

}



