import { Component, OnInit } from '@angular/core';
import { PaymentService } from './../../payment.service'

@Component({
  selector: 'app-cn-by-cash',
  templateUrl: 'cn-by-cash.component.html',
  styleUrls: ['cn-by-cash.component.scss']
})
export class CnByCashComponent implements OnInit {
  public sumPO : number;
  public countSelect : number = 0;
  public data : any = [];
  public stateStatus : boolean = true;
  public summary : number = 0;
  public summary2 : number = 0;
  public cardSuccess : boolean = true

  constructor(
      private service : PaymentService
  ) {

  }

    private checkSelect() : void {
        this.countSelect = 0;
        this.data.map((o, i) => {
            if(o.checked === true) {
                this.countSelect++;
            }
        })
    }

    private onCheckCASH(sapNo: string): void {
      this.service.checked(sapNo, this.service.getSelectCompany(), this, false);
    }

    private checkSummary() : void {
        let val = 0;
        this.cardSuccess = true
        this.data.map((o, i) => {
            if(o.checked === true) {
                if(isNaN(Number(o.billAmt)) || Number(Math.abs(o.billAmt)) === 0) {
                    this.cardSuccess = false
                }
                val += Math.abs(o.billAmt);
            }
        })

        if(val >= this.service.calValueBill()) {
            this.stateStatus = false;
        } else {
            this.stateStatus = true
        }
        this.summary = val;
    }

    private onPutBill(agv: any) {
        this.service.billCard(agv[1] , agv[0] , this.service.getSelectCompany(), this, false);
    }

    public setDataList(data: any = []): void {
        this.data = data;
        this.checkSelect();
        this.checkSummary();
    }



  ngOnInit() {
      this.summary2 = this.service.sumOrder;
      this.service.getPTData(this.service.getSelectCompany(), this, false);
  }

}



